import React, { useEffect, useState } from "react";
import { useForm, Controller } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import {
  TextField,
  Button,
  Container,
  Avatar,
  Typography,
  FormControlLabel,
  Checkbox,
} from "@mui/material";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import { IconButton, InputAdornment } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

import "./LoginDetails.css";

interface LoginForm {
  // Skapar ett interface fr loginformulret
  username: string;
  password: string;
}

interface LoginDetailsProps { // Skapar ett interface fr propsen som skickas in i komponenten
  // Skapar en funktion som tar emot ett argument av typen string och inte returnerar ngot vrde (void)
  //som sedan skickas in i komponenten som en prop med namnet onLogin
  onLogin: (newToken: string) => void; 
}

const LoginDetails: React.FC<LoginDetailsProps> = ({ onLogin }) => {
  
  const {
    handleSubmit, // Anvnds fr att hantera submit av formulret
    control, // Anvnds fr att hantera kontrollerade komponenter
    formState: { errors }, // Formstate anvnds fr att hantera validering av formulret
    setValue, // Anvnds fr att stta vrdet p ett flt i formulret
  } = useForm<LoginForm>(); // useForm anvnds fr att hantera formulret

  const navigate = useNavigate(); // Anvnds fr att kunna navigera till en annan sida
  const [loginError, setLoginError] = useState<string | null>(null); // Stter state fr loginError till null
  const [rememberMe, setRememberMe] = useState(false); // Stter state fr rememberMe till false
  const [showPassword, setShowPassword] = useState(false); // Stter state fr showPassword till false

  const clearLoginError = () => { // Skapar en funktion som stter state fr loginError till null
    setLoginError(null);
  };

  const handleRememberMeChange = ( // Skapar en funktion som stter state fr rememberMe till true eller false beroende p om checkboxen r ikryssad eller inte
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRememberMe(event.target.checked);
  };

  useEffect(() => {
    const rememberedUsername = localStorage.getItem("rememberedUsername"); // Sparar vrdet p rememberedUsername frn localStorage i variabeln rememberedUsername
    const rememberedPassword = localStorage.getItem("rememberedPassword"); // Sparar vrdet p rememberedPassword frn localStorage i variabeln rememberedPassword
    if (rememberedUsername && rememberedPassword) { // Om rememberedUsername och rememberedPassword har ett vrde s krs koden nedan
      setValue("username", rememberedUsername); // Stter vrdet p username till rememberedUsername
      setValue("password", rememberedPassword); // Stter vrdet p password till rememberedPassword
      setRememberMe(true); // Stter state fr rememberMe till true
    }
  }, [setValue]); // setValue r en dependency som anvnds fr att useEffect ska kras nr setValue ndras

  const onSubmit = async (data: LoginForm) => {
    try {
      const response = await fetch("https://app-productmanager-prod.azurewebsites.net/login", { // Skickar in data frn formulret till API:et
        method: "POST",
        headers: { "Content-Type": "application/json" }, // Stter header till application/json
        body: JSON.stringify(data), // Gr om datan till en strng och skickar med den i body
      });

      if (!data.username || !data.password) { // Om username eller password r tomma s krs koden nedan
        setLoginError("Both username and password are required.");
        return;
      }

      if (response.status === 200) { // Om statuskoden r 200 s krs koden nedan
        const responseData = await response.json(); // Sparar datan frn API:et i variabeln responseData

        if (responseData.token) { // Om det finns en token i responseData s krs koden nedan
          const jwtToken = responseData.token; // Sparar token i variabeln jwtToken
          const isAdmin = responseData.isAdmin; // Sparar isAdmin i variabeln isAdmin

          localStorage.setItem("token", jwtToken); // Sparar token i localStorage
          localStorage.setItem("username", responseData.username); // Sparar username i localStorage
          localStorage.removeItem("messageShown"); // Tar bort messageShown frn localStorage
          localStorage.setItem("isAdmin", isAdmin); // Sparar isAdmin i localStorage

          onLogin(jwtToken); // Anropar funktionen onLogin och skickar med jwtToken som argument
          navigate("/Main"); // Navigerar till sidan Main

          console.log("Token:", jwtToken);
        }
      } else {
        setLoginError("Misslyckat inloggningsfrsk. Frsk igen");
      }

      if (rememberMe) { // Om rememberMe r true s krs koden nedan
        localStorage.setItem("rememberedUsername", data.username); // Sparar username i localStorage
        localStorage.setItem("rememberedPassword", data.password); // Sparar password i localStorage
      } else {
        localStorage.removeItem("rememberedUsername"); // Tar bort rememberedUsername frn localStorage
        localStorage.removeItem("rememberedPassword"); // Tar bort rememberedPassword frn localStorage
      }
    } catch (error) { // Om det blir error nr datan ska skickas till API:et s loggas det ut i konsolen
      console.error("Network error:", error); // Loggar ut error i konsolen
    }
  };

  return (
    <>
      <div
        style={{
          backgroundColor: "#d6d5d5",
          boxShadow: "3px 3px 4px rgba(68, 68, 68, 0.5)",
          padding: "30px 0 30px 0",
          width: "30%",
          borderRadius: 5,
          margin: "30px auto",
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Avatar sx={{ bgcolor: "error.main" }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography
          style={{ marginBottom: "25px" }}
          component="h1"
          variant="h5"
        >
          Logga in
        </Typography>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <div style={{ width: "300px" }}>
              <Controller
                name="username"
                control={control}
                defaultValue=""
                rules={{ required: "Anvndarnamn mste vara ifylld" }}
                render={({ field }) => (
                  <>
                    <TextField
                      {...field}
                      label={
                        <span
                          style={{ fontSize: "14px", letterSpacing: "0px" }}
                        >
                          Anvndarnamn
                        </span>
                      }
                      variant="outlined"
                      fullWidth
                      error={Boolean(errors.username)}
                      onFocus={clearLoginError}
                      style={{
                        width: "100%",
                        backgroundColor: "white",
                        borderRadius: "5px",
                        boxShadow: "2px 2px 2px rgba(126, 125, 125, 0.5)",
                      }}
                    />
                  </>
                )}
              />
              {errors.username && (
                <div className="error">{errors.username.message}</div>
              )}
            </div>
            <div style={{ marginTop: "10px", width: "300px" }}>
              <Controller
                name="password"
                control={control}
                defaultValue=""
                rules={{ required: "Lsenord mste vara ifylld" }}
                render={({ field }) => (
                  <>
                    <TextField
                      {...field}
                      label={
                        <span
                          style={{ fontSize: "14px", letterSpacing: "0px" }}
                        >
                          Lsenord
                        </span>
                      }
                      type={showPassword ? "text" : "password"}
                      variant="outlined"
                      fullWidth
                      error={Boolean(errors.password)}
                      onFocus={clearLoginError}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton
                              onClick={() => setShowPassword(!showPassword)}
                              edge="end"
                            >
                              {showPassword ? (
                                <VisibilityOff fontSize="small" color="error" />
                              ) : (
                                <Visibility fontSize="small" color="primary" />
                              )}
                            </IconButton>
                          </InputAdornment>
                        ),
                        style: {
                          width: "100%",
                          backgroundColor: "white",
                          borderRadius: "5px",
                          boxShadow: "2px 2px 2px rgba(126, 125, 125, 0.5)",
                        },
                      }}
                    />
                  </>
                )}
              />
              {errors.password && (
                <div className="error">{errors.password.message}</div>
              )}
            </div>
            <FormControlLabel
              control={
                <Checkbox
                  size="small"
                  value="remember"
                  color="primary"
                  style={{ marginLeft: "-145px", marginTop: "3px" }}
                  onChange={handleRememberMeChange}
                />
              }
              label={
                <span
                  style={{
                    fontSize: "14px",
                    marginLeft: "-5px",
                    letterSpacing: "0px",
                  }}
                >
                  Kom ihg mig
                </span>
              }
            />
            <Button
              type="submit"
              variant="contained"
              color="primary"
              style={{
                height: 44.5,
                width: "300px",
                fontWeight: "bold",
                textTransform: "none",
                letterSpacing: "0px",
                marginTop: "10px",
              }}
            >
              <span>Logga in</span>
            </Button>
          </div>
          <Link
            to={"/RegisterAccount"}
            style={{
              textAlign: "center",
              fontSize: "14px",
              letterSpacing: "0px",
              display: "block",
              marginTop: "10px",
              color: "rgb(1, 103, 255)",
            }}
          >
            Registrera ett nytt konto
          </Link>
          {loginError && (
            <div
              style={{ textAlign: "center", width: "100%", marginTop: "10px" }}
              className="error"
            >
              {loginError}
            </div>
          )}
        </form>
      </div>
    </>
  );
};

export default LoginDetails;
