import React, { useState } from "react";
import { useForm, Controller } from "react-hook-form";
import "./AddCategory.css";
import { Link, useNavigate } from "react-router-dom";
import {
  TextField, Button, Table, TableBody, TableCell,
  TableContainer, TableHead, TableRow, Paper,
  IconButton, Dialog, DialogContent,
} from "@mui/material";
import HomeIcon from "@mui/icons-material/Home";
import { Check, Clear } from "@mui/icons-material";

interface Category {
  name: string;
}

const AddCategory = () => {
  const {
    handleSubmit, // Fr att kunna skicka formulret
    control, // Fr att kunna anvnda react-hook-form med MUI
    formState: { errors }, // Fr att kunna visa felmeddelanden
    reset, // terstller formulret
  } = useForm<Category>(); // Typ av formulrdata

  const navigate = useNavigate(); // Fr att kunna navigera till en annan sida
  const [isRegisterCategoryVisible, setIsRegisterCategoryVisible] = useState(true); // Visar registreringsformulret
  const [isConfirmationPromptVisible, setIsConfirmationPromptVisible] = useState(false); // Visar bekrftelseprompten
  const [isCategorySavedMessageVisible, setIsCategorySavedMessageVisible] = useState(false); // Visar bekrftelsemeddelandet
  const [formData, setFormData] = useState<Category | null>(null); // Formulrdata som ska skickas till API:et
  const [errorName, setErrorName] = useState<string | null>(null); // Felmeddelande fr namn
  const [errorUnauthorized, setErrorUnauthorized] = useState<string | null>(null); // Felmeddelande fr att anvndaren inte r inloggad
  const token = localStorage.getItem("token"); // Hmtar token frn local storage fr att f tillgng till API:et

  const clearError = () => { // Rensar felmeddelandet fr namn
    setErrorName(null);
  };

  const onSubmit = async (data: Category) => { // Skickar formulrdata till API:et
    setIsConfirmationPromptVisible(true); // Visar bekrftelseprompten
    setFormData(data); // Sparar formulrdata i formData
  };

  const handleConfirmYes = async (data: Category) => {
    try {
      const response = await fetch("https://app-productmanager-prod.azurewebsites.net/categories", { // Skickar formulrdata till API:et
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`, // Skickar med token fr att f tillgng till API:et
        },
        body: JSON.stringify(data), // Formulrdata som ska skickas till API:et
      });

      if (response.ok) { // Om kategorin skapades
        reset(); // Rensar formulret
        setIsConfirmationPromptVisible(false); // Gmmer bekrftelseprompten
        setIsCategorySavedMessageVisible(true); // Visar bekrftelsemeddelandet
        setFormData(data); // Sparar formulrdata i formData

        setTimeout(() => { // Timer fr att visa bekrftelsemeddelandet
          setIsCategorySavedMessageVisible(false); // Gmmer bekrftelsemeddelandet
          navigate("/Main"); // Navigerar till Main
        }, 2000); // Efter 2 sekunder
      } else if (response.status === 400) { // Om Kategorin redan finns
        setIsConfirmationPromptVisible(false); // Gmmer bekrftelseprompten
        setIsRegisterCategoryVisible(true); // Visar registreringsformulret
        setErrorName("Namn finns redan"); // Visar felmeddelande fr namn
      }
    } catch (error) { // Om ngot gick fel
      setErrorUnauthorized("Ej behrighet fr utfrande "); // Visar felmeddelande fr att anvndaren inte r inloggad
      console.error("Error:", error); // Skriver ut felmeddelande i konsolen
      setIsConfirmationPromptVisible(false); // Gmmer bekrftelseprompten
    }
  };

  const handleConfirmNo = () => {
    setIsConfirmationPromptVisible(false); // Gmmer bekrftelseprompten
    setIsRegisterCategoryVisible(true); // Visar registreringsformulret
    reset(); // Rensar formulret
  };

  return (
    <>
      {isRegisterCategoryVisible && (
        <div className="register-category-container">
          <div className="icon-button-container">
            <IconButton size="small" component={Link} to="/Main">
              <HomeIcon />
            </IconButton>
          </div>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="form-container">
              <Controller
                name="name"
                control={control}
                defaultValue=""
                rules={{ required: "Namn mste vara ifylld." }}
                render={({ field }) => (
                  <>
                    <TextField
                      {...field}
                      label={<span style={{ fontSize: "14px" }}>Namn</span>}
                      variant="outlined"
                      fullWidth
                      error={Boolean(errors.name) || Boolean(errorName)}
                      onFocus={clearError}
                      style={{
                        width: "300px",
                        backgroundColor: "white",
                        borderRadius: "5px",
                        boxShadow: "2px 2px 2px rgba(126, 125, 125, 0.5)",
                      }}
                    />
                    {errors.name && (
                      <div className="error">{errors.name.message}</div>
                    )}
                    {errorName && <div className="error">{errorName}</div>}
                    {errorUnauthorized && (
                      <div className="error">{errorUnauthorized}</div>
                    )}
                  </>
                )}
              />
              <Button
                type="submit"
                variant="contained"
                color="primary"
                style={{
                  marginTop: 10,
                  height: 44.5,
                  width: "300px",
                  fontWeight: 600,
                  textTransform: "none",
                  letterSpacing: "0px",
                }}
              >
                <span>Registrera Kategori</span>
              </Button>
            </div>
          </form>
        </div>
      )}

      {isConfirmationPromptVisible && (
        <Dialog
          open={isConfirmationPromptVisible}
          onClose={handleConfirmNo}
          maxWidth="md"
          fullWidth
        >
          <DialogContent
            style={{
              backgroundColor: "#d6d5d5",
              padding: "30px 0 30px 0",
              borderRadius: 5,
              margin: "30px auto",
              boxShadow: "3px 3px 4px rgba(68, 68, 68, 0.5)",
              display: "flex",
              justifyContent: "center",
              flexDirection: "column",
              alignItems: "center",
              textAlign: "center",
              width: "93%",
              fontWeight: 600,
            }}
          >
            <span>r detta korrekt?</span>
            <TableContainer
              component={Paper}
              style={{
                marginTop: "20px",
                border: "none",
                boxShadow: "3px 3px 4px rgba(68, 68, 68, 0.5)",
                width: "300px",
                margin: "20px auto",
              }}
            >
              <Table>
                <TableHead className="tablehead">
                  <TableRow>
                    <TableCell
                      style={{
                        color: "#fff",
                        fontWeight: "bold",
                        textAlign: "center",
                      }}
                    >
                      <span>Namn</span>
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {formData && (
                    <TableRow className="alternate-row">
                      <TableCell style={{ textAlign: "center" }}>
                        {formData.name}
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
            <div className="button-container">
              <Button
                color="primary"
                variant="contained"
                onClick={() => {
                  if (formData) {
                    handleConfirmYes(formData);
                  }
                }}
                style={{
                  marginRight: 5,
                  borderRadius: "10rem",
                }}
              >
                <Check />
              </Button>
              <Button
                color="error"
                variant="contained"
                onClick={handleConfirmNo}
                style={{ borderRadius: "10rem" }}
              >
                <Clear />
              </Button>
            </div>
          </DialogContent>
        </Dialog>
      )}

      {isCategorySavedMessageVisible && (
        <Dialog
          open={isCategorySavedMessageVisible}
          onClose={handleConfirmNo}
          maxWidth="sm"
          fullWidth
        >
          <DialogContent
            style={{
              backgroundColor: "#2979ff",
              padding: "30px 0 30px 0",
              borderRadius: 5,
              margin: "30px auto",
              boxShadow: "3px 3px 4px rgba(68, 68, 68, 0.5)",
              display: "flex",
              justifyContent: "center",
              flexDirection: "column",
              alignItems: "center",
              textAlign: "center",
              fontWeight: 600,
              color: "#fff",
              width: "90%",
            }}
          >
            <span>Kategori sparad</span>
          </DialogContent>
        </Dialog>
      )}
    </>
  );
};

export default AddCategory;
